import { Controller, Block, Document, Text } from '@ali/4ever-cangjie';
import { getPointAndBlockOnSelection } from '@ali/4ever-utils';

export const getExtendBlock = (doc: Document, block: Block | null): Block | null => {
  if (!block) {
    return null;
  } else if (block.type) {
    return block;
  } else {
    return getExtendBlock(doc, doc.getParent(block.key) as Block);
  }
};

/**
 * @description 继承粘贴时光标所在位置 startBlock的style及type
 * @param {Document} fragment - 粘贴要插入的内存模型对象
 * */
export default function getMatchFragment(
  controller: Controller,
  fragment: Document,
): Document {
  const { document } = controller.value;
  const pointAndBlock = getPointAndBlockOnSelection(controller);
  const { startBlock, startPoint } = pointAndBlock;
  const extendBlock = getExtendBlock(controller.value.document, startBlock as Block);
  if (!startBlock) return fragment;
  const startBlockPath = document.getPath(startBlock.key);
  const pasteFromStart =
    startBlockPath &&
    startBlockPath[1] === 0 &&
    startPoint.isAtStartOfNode(startBlock);
  const marks = (startBlock as Text)?.marks;
  if (marks) {
    // @ts-ignore
    const newFragment = fragment.mapDescendants(child => {
      if (Text.isText(child)) {
        const childMarks = child.marks;
        childMarks.forEach(mark => {
          child = (child as Text).removeMark(0, child.text.length, mark);
        });
        return pasteFromStart
          ? child
          : child.addMarks(0, child.text.length, marks);
      } else if (extendBlock) {
        if ((child as Block)?.type === 'paragraph') {
          return (child as Block)
            .set('type', extendBlock.type)
            .set('data', extendBlock.data);
        }
        return child;
      }
      return undefined;
    });
    return newFragment;
  } else {
    return fragment;
  }
}
